.ama__membership {
  background: #ece7f0;
  @include gutter($padding-bottom-full...);
  @include child-top-gutters($gutter / 2);
  display: flex;
  flex-direction: column;

  &.bottom-margin {
    @include gutter($margin-bottom-full...);
  }

  // Remove margins on child elements so that we can set consistent spacing elsewhere.
  & > * {
    margin-bottom: 0;

    &:not(.membership_image) {
      &:not(.ama__button) {
        &:not(.ama__membership_cta_container) {
          @include gutter($padding-left-full...);
          @include gutter($padding-right-full...);
        }
      }
    }
  }

  .ama__h2 {
    margin-bottom: 0;
  }

  &_logo-container {
    background-color: #46166b;
    min-height: 28px;
    display: flex;

    .ama__membership_logo {
      margin-top: 4px;
      margin-bottom: 4px;
      max-height: 40px;

      img {
        height: 40px;
        width: auto;
      }
    }
  }

  > .membership_image {
    margin: 0;

    img {
      width: 100%;
      height:auto;
      margin-top: 0;
    }
  }

  .ama__membership_cta_container {
    display: block;
    margin: 14px 28px 0px 28px;
    @include breakpoint($bp-med min-width) {
      margin-top: 14px;
    }
    .ama__button {
      margin: 0;
      width: 100%;
    }
  }
  @extend .display-list;
}

a.ama__membership {
  display: block;
  text-decoration: none;

  &:hover {
    color: $black;
    background: darken(#DAD5DE, 8%);
  }
}

//Hide ama logo in membership block in all instances besides below
.ama__membership_logo-container .logo {
  display: none;
}

//Slim Membership Block styling
.layout--ama-page-section-one-col .ama__membership {
  background: #ece7f0;
  height: 210px;
  margin: 0 0 35px;
  position: relative;
  padding-bottom: 0;
  @include breakpoint($bp-small min-width) {
    width: 100%;
    height: unset;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }

  // Remove margins on child elements so that we can set consistent spacing elsewhere.
  & > * {
    margin-bottom: 0;

    &:not(.membership_image) {
      &:not(.ama__button) {
        &:not(.ama__membership_cta_container) {
          padding: unset;
        }
      }
    }
  }

  //hide description
  .membership-body {
    display: none;
  }

  .ama__membership_logo-container {
    display: block;
    width: 100%;
    height: 40px;
    position: relative;
    padding: 0;
    @include breakpoint($bp-small min-width) {
      width: 102px;
      height: 44px;
    }
    @include breakpoint($bp-med min-width) {
      width: 200px;
      height: 70px; 
    }

    .logo {
      display: block;
      height: 31px;
      width: 76px;
      position: absolute;
      top: 15%;
      left: 2.3%;
      @include breakpoint($bp-small min-width) {
        left: 12%;
      }
      @include breakpoint($bp-med min-width) {
        top: 20%;
        left: 25%;
        height: 42px;
        width: 105px;
      }
    }

    .ama__site-logo {
      display: block;
      position: relative;
      height: 44px;
      @include breakpoint($bp-med min-width) {
        height: 70px;
      }
    }
  }
  .ama__membership_title_container {
    display: block;
    padding-left: 0;
    padding-right: 0;
    margin: 0;
    @include breakpoint($bp-small min-width) {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-flex: 2;
      -ms-flex: 2;
      flex: 2.28;
      flex-grow: 6;
    }
  }
  .ama__membership_cta_container {
    margin: 0 auto;
    display: block;
    max-width: 75%; 
    text-align: center;
    position: absolute;
    bottom: 21px;
    left: 0;
    right: 0;
    @include breakpoint($bp-small min-width) {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      position: relative;
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;
      flex-grow: 2.28;
      text-align: right;
      position: relative;
      bottom: 0;
    }
    @include breakpoint($bp-med min-width) {
      flex-grow: 3;
    }
    @include breakpoint($bp-large min-width) {
      flex-grow: 2;
    }
  }
  h2.ama__h2 {
    margin-top: 14px;
    margin-bottom: 0;
    padding-left: 16%;
    padding-right: 16%;
    text-align: center;
    font-size: 26px;
    @include breakpoint($bp-small min-width) {
      margin-top: 0;
      padding: 0;
    }
    @include breakpoint($bp-med min-width) {
      font-size: 1.444em;
    }
  }
  a.ama__button {
    padding: 0 22px;
    font-size: 18px;
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    width: unset;
    overflow: hidden;
    @include breakpoint($bp-small min-width) {
      display: inline-block;
      margin: 0;
      padding: 0 12px;
      height: 44px;
      line-height: 44px;
      font-size: 15px;
    }
    @include breakpoint($bp-med min-width) {
      font-size: 20px;
      height: 50px;
      line-height: 50px;
      margin-right: 28px;
    }
  }
}

.layout__region--rail_right {
  .ama__membership {
    margin-bottom: 21px;
    @include breakpoint($bp-small min-width) {
      margin-bottom: 28px;
    }
    @include breakpoint($bp-large min-width) {
      margin-bottom: 56px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

@include breakpoint($bp-small max-width) {
  .ama__layout--two-col-right--75-25__right {
    .ama__membership {
      position: relative;
      margin-left: -25px;
      margin-right: -14px;
    }
  }
  .ama__category-index.ama__layout--two-col-right--75-25 {
    .ama__membership {
      position: relative;
      margin-left: -22px;
      margin-right: -22px;
    }
  }
  .ama__category-index.ama__category__row--no-border-with-padding {
    .ama__membership {
      position: relative;
      margin-left: -40px;
      margin-right: -30px;
    }
  }
}
